.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogsItems{
    padding: 10px;
}
.dialogsItems .active{
    color: gold;
}
.messages{
    padding: 10px;
}
.messages .message{
    color: white;
}

.dialogsItems a{
    color: #333;
    text-decoration: none;
}